.reveal .reveal__content {
  overflow: hidden;
  height: 0;
  transition: transition(height, opacity, transform);
}
.reveal.is-enabled .reveal__content {
  height: auto;
  opacity: 0;
  transform: translateY(var(--reveal--translateY, 0px));
}
.reveal.is-open .reveal__content {
  opacity: 1;
  transform: translateY(0);
}